html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: #F0F0F0
}

form {
	display: grid;
	grid-template-columns: 325px 325px;
	grid-column-gap: 100px;	
	grid-row-gap: 20px;
	justify-content: center;
	align-items: center;
	margin-top: 6%
}

.submit {
	background-color: #FC08D5; /* Pink */
	border: none;
	color: white;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	height: 32px;
	width: 80px;
}

.disclaimer {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	font-size: .6em;
	grid-column: 1 / 3;
}
.header{
    padding-top: 10px;
    padding-left: 10px;
}

.comments {
    width: 40%;
}
:root {
    --yellow: #ffc600;
    --black: #272727;
}

.mag-container {
    display: grid;
    align-content: center;
    width: 100%;
    grid-template-columns: repeat(6, auto);
    grid-gap:20px;
    padding-top: 18%;
}

a:link, a:visited {
    color: black;
}

.mag {
    display: grid;
    justify-content: center;
    text-decoration: none;
    align-items: center;
    border-radius: 3px;
    font-size: 35px;
}
.image {
    margin: auto auto 15px 15px;
    display: inline-block;
    height: 400px;
}

.image-container {
    padding-top: 8%;
    display: inline-block;
    white-space: nowrap;
    overflow: visible;
}
